          {{ image.name }}
        </li>
      </ul>
    </div>
    <div style="float: right; width: 80%;">
      <OpenSeadragonViewer :image-index="this.imageIndex" />
    </div>
  </div>
</template>

<script>
import OpenSeadragonViewer from './components/OpenSeadragonViewer.vue';

export default {
