<template>
    <div id="viewer" class="openseadragon-viewer"></div>
</template>
  
  <script>
  import OpenSeadragon from "Openseadragon";
  
  export default {
    name: "OpenSeadragonViewer",
    props: {
    // imageSource: {
    //     type: String,
    //     required: true
    //   }
        imageIndex: {
            // type: Int32Array,
            required: true
        }
    },
    data() {
        return {
            images: [
                { name: 'Image 1', source: 'http://openseadragon.github.io/example-images/highsmith/highsmith.dzi'},
                { name: 'Image 2', source: 'http://openseadragon.github.io/example-images/highsmith/highsmith.dzi'}
            ],
            viewer: null
        }
    },
    watch: {
        imageIndex(newVal, oldVal) {
            if (this.viewer) {
                this.viewer.close();
                this.viewer.destroy();
            }
            this.initViewer();
            console.log(newVal);
            console.log(oldVal);
        }
    },
    mounted() {
        console.log(this.images[this.imageIndex].source);
      this.initViewer();
      
    },
    methods: {
      initViewer() {
        // 初始化 OpenSeadragon
        this.viewer = OpenSeadragon({
          id: 'viewer', // 设置 viewer 容器
          tileSources: this.images[this.imageIndex].source,
         // tileSources: "http://openseadragon.github.io/example-images/highsmith/highsmith.dzi",
        //   tileSources: {
        //     Image: {
        //         xmlns: 'http://schemas.microsoft.com/deepzoom/2008',
        //         Url: '/images/test_files/',
        //         Overlap: '1',
        //         Format: 'jpeg',
        //         TileSize: '254',
        //         Size: {
        //             Height: '32893',
        //             Width: '46000'
        //         }
        //     }
        //   },
          prefixUrl: '/images-icon/', // 图标路径
          showNavigator: true, // 显示导航栏
          showZoomControl: true, // 显示缩放控件
          crossOriginPolicy: 'Anonymous'
          // maxZoomPixelRatio: 2, // 最大缩放比
        });
      }
    },
    beforeUnmount() {
      // 在组件销毁前清理 OpenSeadragon 实例
      if (this.viewer) {
        this.viewer.destroy();
      }
    }
  };
  </script>
  
  <style scoped>
  .openseadragon-viewer {
    width: 100%;
    height: 600px; /* 或者你可以设置为其他适合的高度 */
  }
  </style>
  